body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite time-minute;
  position: absolute;
  bottom: 50%;
}

@keyframes time-minute {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: time-second;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
}

@keyframes time-second {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.stopwatch__center {
  box-sizing: border-box;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}
